















import {mapState, mapMutations} from 'vuex';
export default {
	data() {
		return {
			imgs: [
				"../../static/img/ic_splash.jpg"
			]
		};
	},
	onLoad() {
	setInterval(() => {
		this.skipIndex();
		if(this.hasLogin){
			uni.reLaunch({		
				url: '../index/index'
			});
		}else{
			uni.reLaunch({
				url:'../login/login/login'
			});
		}
	}, 3);	
	},
	computed:mapState(['hasSkip','hasLogin']),
	methods:{
		...mapMutations(['skipIndex']),
		bindSkip(){
			this.skipIndex();
			if(this.hasLogin){
				uni.reLaunch({		
					url: '../index/index'
				});
			}else{
				uni.reLaunch({
					url:'../login/login/login'
				});
			}
			
		}
	},
	onLoad() {
		if(this.hasSkip){
			if(this.hasLogin){
				uni.reLaunch({		
					url: '../index/index'
				});
			}else{
				uni.reLaunch({
					url:'../login/login/login'
				});
			}
		}
	}
}
